<template>
  <div>
    <form id="" class="editInputWrap" >
      <label for="" class="submitFileLabel">
        height:
      </label>
      <input class="editInput" type="number" name="" v-model="thisState.thisHeight" placeholder="height(px)">

      <label for="" class="submitFileLabel">
      width:
      </label>
      <input class="editInput" type="number" name="" v-model="thisState.thisWidth" placeholder="width(px)">

      <label for="" class="submitFileLabel">
      left:
      </label>
      <input class="editInput" type="number" name="" v-model="thisState.thisLeft" placeholder="left(px)">

      <label for="" class="submitFileLabel">
      top:
      </label>
      <input class="editInput" type="number" name="" v-model="thisState.thisTop" placeholder="(px)">
      <label for="" class="submitFileLabel">
      :
      </label>
      <input class="editInput" type="number" name="" v-model="thisState.thisBorderRadius" placeholder="top(px)">
      <label for="" class="submitFileLabel">
        :
      </label>
      <select v-model="thisState.selected">
        <option disabled value="default"></option>
        <option value="open"></option>
        <option value="twitter">twitter</option>
        <option value="line">line</option>
        <option value="vedio"></option>
        <option value="mail"></option>
      </select>
        <label for="" v-show="thisState.selected==='open'" class="submitFileLabel">
          SID:
        </label>
        <input class="editInput" v-show="thisState.selected==='open'" type="number" name="" v-model="thisState.thisSid" placeholder="SID">

      <!--<span>Selected: {{ selected }}</span>-->

      <div class="closeBtn" @click="closeDragAbleInfo">&times;</div>
      <button type="button" class="submitDragAbleInfoBtn btn" @click="submitDragAbleInfo"></button>
    </form>
  </div>
</template>

<script>
  import {mapState, mapGetters, mapMutations} from 'vuex'

  export default {
    props:['item'],
    data() {
      return {
        thisState: {
          thisHeight: '',
          thisWidth: '',
          thisLeft: '',
          thisTop: '',
          thisBorderRadius: '',
          selected: '',
          thisSid:''
        }
      }
    },
    beforeMount(){
      this.addOrMoveEndChangeData()
    },
    watch: {
      item:{
        handler(oldValue,newValue){//watch
          console.log('------------')
          this.addOrMoveEndChangeData()
        },
        deep:true
      },
      'thisState.selected':{//
        handler(newValue,oldValue){
          if(newValue!=='open'){
            this.thisState.thisSid='';
//            console.log(this.thisState);
          }
        }
      }
    },
    methods:{
      submitDragAbleInfo(){
        let formData= new FormData();
//        console.log(this.thisState);
//        formData.append('thisHeight')
        this.$store.commit('submitDragAbleInfo',{item:this.item,it:this.thisState});

        this.closeDragAbleInfo();
      },
      closeDragAbleInfo(){

        this.$store.commit('changeShow',this.item)
      },
      addOrMoveEndChangeData(){
        let obj={};
        for(let p in this.item.info){
          if(p==='thisSid'){
            obj[p]= this.item.info[p]//sid
          }else{
            obj[p]= remToPx(this.item.info[p])
          }
        }
        this.thisState={...this.thisState,...obj}//form
      }
    },
    computed: {
      ...mapGetters(['getDragAbleBtnInfo'])
    }
  }

</script>
<style scoped lang="scss">

</style>


